.container{width:100%;height:100%;display:flex}.logo-section{width:100px;height:100%;display:flex;justify-content:center;align-items:center}.logo-section>a{width:65%;height:65%}.home-link{width:50px;height:50px;background:var(--headerLogo);border-radius:100%}.options-section{flex-grow:1;display:flex;justify-content:center;align-items:center}.link{font-size:1.5em;padding:0px 7.5px}@media screen and (max-width: 640px){.logo-section{width:75px}.link{font-size:1em;padding:0 5px}}