.container{width:100%;display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;padding-bottom:30px}.section{width:100%;margin:20px 0 0 0;padding:10px 45px;border:3px solid var(--borderColorMain);color:var(--fontColorMain);background-color:var(--bgColorAlt);opacity:.8;border-radius:30px}.page-options{width:80%;display:flex;justify-content:center}.page-option{margin:0px 5px}@media screen and (max-width: 640px){.container{font-size:.8em}.section{padding:10px 0px}}@media screen and (min-width: 641px)and (max-width: 1280px){.section{padding:10px 20px}}